import React from "react";
import {
  BsCheckSquareFill,
  BsCheckCircleFill,
  BsArrowRightSquareFill,
  BsArrowRightCircleFill,
} from "react-icons/bs";
import { QuestionAnswersStore } from "../../../actions";
import {
  CalculatedMultipleChoiceQuestionAnswerInterface,
  MatchingQuestionAnswerInterface,
  MultipleChoiceQuestionAnswerInterface,
  QuestionInterface,
  questionType,
  questionTypeElementsType,
  TrueFalseQuestionAnswerInterface,
  NumericalQuestionAnswerInterface,
  ShortAnswerQuestionAnswerInterface,
  SimpleCalculatedQuestionAnswerInterface,
  QuestionLanguage,
  LanguageInterface,
} from "../../../interfaces";
import {
  toCalculatedMultipleChoice,
  toMatching,
  toMultipleChoice,
  toNumerical,
  toShortAnswer,
  toSimpleCalculated,
  toTrueFalse,
} from "../QuestionDetails/ConvertingAnswers";
import { findQuestionType } from "../utils/utils";

interface FindAnswerProps {
  question: QuestionInterface;
  answersList: QuestionAnswersStore[];
  selectedLanguage: QuestionLanguage | LanguageInterface;
  questionTypes: questionType[];
  showCorrectAnswer: boolean;
  onClick: () => void;
}

const FindAnswer = (props: FindAnswerProps) => {
  const answer = props.answersList.find(
    (item) => item.question_id === props.question.question_id
  );
  const UserAnswersSel = props.question.question.find(
    (itm) => itm.language === props.selectedLanguage
  );
  if (answer === undefined || UserAnswersSel === undefined) {
    return (
      <div className="text-yellow-600 font-extrabold text-2xl text-center">
        No answer found!
      </div>
    );
  } else {
    switch (findQuestionType(props.question.type, props.questionTypes)) {
      case questionTypeElementsType.MultipleChoice:
        const userAnswer: MultipleChoiceQuestionAnswerInterface[] =
          toMultipleChoice(answer.answer);
        return (
          <div className="w-full">
            {userAnswer
              .filter((itm) => itm.status === true)
              .map((item, i) => (
                <div
                  key={i + 1}
                  onClick={props.onClick}
                  className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                >
                  <div className="text-xl text-blue-600">
                    <BsCheckSquareFill />
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: item.value }}></div>
                </div>
              ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer
                    .filter((itm) => itm.status === true)
                    .map((item, i) => (
                      <div
                        key={i + 1}
                        onClick={props.onClick}
                        className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                      >
                        <div className="text-xl text-blue-600">
                          <BsCheckSquareFill />
                        </div>
                        <div
                          dangerouslySetInnerHTML={{ __html: item.value }}
                        ></div>
                      </div>
                    ))}
                </div>
              </div>
            )}
          </div>
        );
      case questionTypeElementsType.SingleMultiple:
        const userAnswerSingle: MultipleChoiceQuestionAnswerInterface[] =
          toMultipleChoice(answer.answer);
        return (
          <div className="w-full">
            {userAnswerSingle
              .filter((itm) => itm.status === true)
              .map((item, i) => (
                <div
                  key={i + 1}
                  onClick={props.onClick}
                  className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                >
                  <div className="text-xl text-blue-600">
                    <BsCheckCircleFill />
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: item.value }}></div>
                </div>
              ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer
                    .filter((itm) => itm.status === true)
                    .map((item, i) => (
                      <div
                        key={i + 1}
                        onClick={props.onClick}
                        className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                      >
                        <div className="text-xl text-blue-600">
                          <BsCheckCircleFill />
                        </div>
                        <div
                          dangerouslySetInnerHTML={{ __html: item.value }}
                        ></div>
                      </div>
                    ))}
                </div>
              </div>
            )}
          </div>
        );

      case questionTypeElementsType.CalculatedMultipleChoice:
        const userAnswer1: CalculatedMultipleChoiceQuestionAnswerInterface[] =
          toCalculatedMultipleChoice(answer.answer);
        return (
          <div className="w-full">
            {userAnswer1
              .filter((itm) => itm.status === true)
              .map((item, i) => (
                <div
                  key={i + 1}
                  onClick={props.onClick}
                  className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                >
                  <div className="text-xl text-blue-600">
                    <BsCheckSquareFill />
                  </div>
                  <div className="text-base font-normal text-gray-500">
                    {item.value.length > 1 ? (
                      <>
                        Between{" "}
                        <span className="underline text-gray-600 font-extrabold">
                          {item.value[0]}
                        </span>{" "}
                        and{" "}
                        <span className="underline text-gray-600 font-extrabold">
                          {item.value[1]}
                        </span>
                      </>
                    ) : (
                      <span className="text-gray-700">
                        {item.value[0].toString()}
                      </span>
                    )}
                  </div>
                </div>
              ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer
                    .filter((itm) => itm.status === true)
                    .map((item, i) => (
                      <div
                        key={i + 1}
                        onClick={props.onClick}
                        className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 mb-1 py-3 font-bold"
                      >
                        <div className="text-xl text-blue-600">
                          <BsCheckSquareFill />
                        </div>
                        <div className="text-base font-normal text-gray-500">
                          {item.value.length > 1 ? (
                            <>
                              Between{" "}
                              <span className="underline text-blue-600 font-extrabold">
                                {item.value[0]}
                              </span>{" "}
                              and{" "}
                              <span className="underline text-blue-600 font-extrabold">
                                {item.value[1]}
                              </span>
                            </>
                          ) : (
                            <span className="text-blue-600">
                              {item.value[0].toString()}
                            </span>
                          )}
                        </div>
                      </div>
                    ))}
                </div>
              </div>
            )}
          </div>
        );

      case questionTypeElementsType.Matching:
        const userAnswerMatch: MatchingQuestionAnswerInterface[] = toMatching(
          answer.answer
        );
        return (
          <div className="w-full">
            {userAnswerMatch.map((item, i) => (
              <div
                key={i + 1}
                onClick={props.onClick}
                className="bg-gray-100 rounded cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 font-bold border border-blue-300"
              >
                <div className="text-xl text-blue-600 font-bold p-3 pr-1 md:pr-3">
                  <div className="h-8 w-8 p-3 bg-blue-100 rounded-full flex items-center justify-center">
                    {i + 1}
                  </div>
                </div>
                <div className="flex flex-col lg:flex-row items-center gap-3 w-full h-full truncate">
                  <div
                    className="w-full mt-3 lg:mb-0"
                    dangerouslySetInnerHTML={{ __html: item.value }}
                  ></div>
                  <div className="flex flex-row items-center gap-3 w-full mb-3 lg:mb-0">
                    <div className="text-3xl text-gray-500 group-hover:text-blue-600">
                      <BsArrowRightSquareFill />
                    </div>
                    <div
                      className="w-full mr-2"
                      dangerouslySetInnerHTML={{ __html: item.status }}
                    ></div>
                  </div>
                </div>
              </div>
            ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer.map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={props.onClick}
                      className="bg-blue-50 rounded cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 font-bold border border-blue-300"
                    >
                      <div className="text-xl text-blue-600 font-bold p-3 pr-1 md:pr-3">
                        <div className="h-8 w-8 p-3 bg-blue-100 rounded-full flex items-center justify-center">
                          {i + 1}
                        </div>
                      </div>
                      <div className="flex flex-col lg:flex-row items-center gap-3 w-full h-full truncate">
                        <div
                          className="w-full mt-3 lg:mb-0"
                          dangerouslySetInnerHTML={{ __html: item.value }}
                        ></div>
                        <div className="flex flex-row items-center gap-3 w-full mb-3 lg:mb-0">
                          <div className="text-3xl text-gray-500 group-hover:text-blue-600">
                            <BsArrowRightSquareFill />
                          </div>
                          <div
                            className="w-full mr-2"
                            dangerouslySetInnerHTML={{ __html: item.status }}
                          ></div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        );
      case questionTypeElementsType.TrueFalse:
        const userAnswerTureFalse: TrueFalseQuestionAnswerInterface[] =
          toTrueFalse(answer.answer);
        return (
          <div className="w-full">
            {userAnswerTureFalse
              .filter((itm) => itm.status === true)
              .map((item, i) => (
                <div
                  key={i + 1}
                  onClick={props.onClick}
                  className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
                >
                  <div className="text-xl">
                    <BsArrowRightCircleFill />
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: item.value }}></div>
                </div>
              ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer
                    .filter((itm) => itm.status === true)
                    .map((item, i) => (
                      <div
                        key={i + 1}
                        onClick={props.onClick}
                        className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-blue-600 hover:text-blue-600 mb-1 py-3 font-bold"
                      >
                        <div className="text-xl">
                          <BsArrowRightCircleFill />
                        </div>
                        <div
                          dangerouslySetInnerHTML={{ __html: item.value }}
                        ></div>
                      </div>
                    ))}
                </div>
              </div>
            )}
          </div>
        );
      case questionTypeElementsType.Numeric:
        const userAnswerNumerical: NumericalQuestionAnswerInterface[] =
          toNumerical(answer.answer);
        return (
          <div className="w-full">
            {userAnswerNumerical.map((item, i) => (
              <div
                key={i + 1}
                onClick={props.onClick}
                className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
              >
                <div className="text-xl">
                  <BsArrowRightCircleFill />
                </div>
                <div className="text-gray-600 text-base font-normal">
                  {item.value.length > 1 ? (
                    <>
                      Between{" "}
                      <span className="underline text-gray-600 font-extrabold">
                        {item.value[0]}
                      </span>{" "}
                      and{" "}
                      <span className="underline text-gray-600 font-extrabold">
                        {item.value[1]}
                      </span>
                    </>
                  ) : (
                    item.value.length > 0 && (
                      <span className="text-gray-700">
                        {item.value[0].toString()}
                      </span>
                    )
                  )}
                </div>
              </div>
            ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer.map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={props.onClick}
                      className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-blue-600 hover:text-blue-600 mb-1 py-3 font-bold"
                    >
                      <div className="text-xl">
                        <BsArrowRightCircleFill />
                      </div>
                      <div className="text-gray-600 text-base font-normal">
                        {item.value.length > 1 ? (
                          <>
                            Between{" "}
                            <span className="underline text-blue-600 font-extrabold">
                              {item.value[0] - item.value[1]}
                            </span>{" "}
                            and{" "}
                            <span className="underline text-blue-600 font-extrabold">
                              {item.value[0] + item.value[1]}
                            </span>
                          </>
                        ) : (
                          item.value.length > 0 && (
                            <span className="text-blue-700">
                              {item.value[0].toString()}
                            </span>
                          )
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        );
      case questionTypeElementsType.SimpleCalculate:
        const userAnswerSimpleCalculate: SimpleCalculatedQuestionAnswerInterface[] =
          toSimpleCalculated(answer.answer);
        return (
          <div className="w-full">
            {userAnswerSimpleCalculate.map((item, i) => (
              <div
                key={i + 1}
                onClick={props.onClick}
                className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
              >
                <div className="text-xl">
                  <BsArrowRightCircleFill />
                </div>
                <div className="text-gray-600 text-base font-normal">
                  {item.value.length > 1 ? (
                    <>
                      Between{" "}
                      <span className="underline text-gray-600 font-extrabold">
                        {item.value[0]}
                      </span>{" "}
                      and{" "}
                      <span className="underline text-gray-600 font-extrabold">
                        {item.value[1]}
                      </span>
                    </>
                  ) : (
                    item.value.length > 0 && (
                      <span className="text-gray-700">
                        {item.value[0].toString()}
                      </span>
                    )
                  )}
                </div>
              </div>
            ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer.map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={props.onClick}
                      className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-blue-600 hover:text-blue-600 mb-1 py-3 font-bold"
                    >
                      <div className="text-xl">
                        <BsArrowRightCircleFill />
                      </div>
                      <div className="text-gray-600 text-base font-normal">
                        {item.value.length > 1 ? (
                          <>
                            Between{" "}
                            <span className="underline text-blue-600 font-extrabold">
                              {item.value[0]}
                            </span>{" "}
                            and{" "}
                            <span className="underline text-blue-600 font-extrabold">
                              {item.value[1]}
                            </span>
                          </>
                        ) : (
                          item.value.length > 0 && (
                            <span className="text-blue-700">
                              {item.value[0].toString()}
                            </span>
                          )
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        );
      case questionTypeElementsType.ShortAnswer:
        const userAnswerShortAnswer: ShortAnswerQuestionAnswerInterface[] =
          toShortAnswer(answer.answer);
        return (
          <div className="w-full">
            {userAnswerShortAnswer.map((item, i) => (
              <div
                key={i + 1}
                onClick={props.onClick}
                className="bg-gray-100 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-gray-600 hover:text-blue-600 mb-1 py-3 font-bold"
              >
                <div className="text-xl">
                  <BsArrowRightCircleFill />
                </div>
                <div
                  dangerouslySetInnerHTML={{
                    __html: item.value,
                  }}
                ></div>
              </div>
            ))}
            {/* Correct answers */}
            {props.showCorrectAnswer === true && (
              <div className="border-l-4 rounded-xl border-blue-600 bg-white p-3">
                <div className="font-extrabold text-lg">Correct answer</div>
                <div>
                  {UserAnswersSel.answer.map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={props.onClick}
                      className="bg-blue-50 rounded px-3 cursor-pointer flex flex-row items-center gap-3 text-blue-600 hover:text-blue-600 mb-1 py-3 font-bold"
                    >
                      <div className="text-xl">
                        <BsArrowRightCircleFill />
                      </div>
                      <div
                        dangerouslySetInnerHTML={{
                          __html: item.value,
                        }}
                      ></div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        );
      default:
        return <div className="text-blue-600 font-extrabold">Answer added</div>;
    }
  }
};

export default FindAnswer;
